import React, { useContext, useEffect, useState } from 'react';
import { styled } from '@mui/material/styles';
import { Box, Typography, Grid, List, ListItem, Divider, Card, CardActionArea, CardMedia, CardContent, CardActions, Button, Dialog, DialogTitle, DialogContent, TextField, DialogActions, ListItemButton, ListItemText } from '@mui/material';
import { useTheme } from '@mui/material/styles';
import Header from '../components/header';
import { Link, Screen, Section } from '../components/screen';
import Footer from '../components/footer';
import { NavLink } from 'react-router-dom';
import { blueGrey } from '@mui/material/colors';

type Video = {
    title: string;
    genre: string;
    length: string;
    file: string;
};

const Playlist: Video[] = [
    { title: "Follow the Light", genre: "Trance", length: "2:36", file: "follow_the_light.mp4" },
    { title: "refluster 2004-2006 reel", genre: "Misc", length: "7:07", file: "refluster_reel.mp4" },
];

const VideoWorksPage: React.FC = () => {
    const [curVideo, setCurVideo] = useState<Video | null>(null);

    const videoSelect = (video: Video) => {
        setCurVideo(video);
        console.log(curVideo);
        setTimeout(() => {
            console.log(curVideo);
        }, 0);
    }

    return (
        <Screen>
            <Header title={"Video Works"} />
            <Section>
                <video controls src={curVideo ? `${process.env.PUBLIC_URL}/mp4/${curVideo.file}` : undefined} style={{
                    width: '100%',
                }} />
                <Box sx={{ flex: 1, display: 'flex', flexDirection: 'column', maxHeight: 320, overflow: 'auto' }}>
                    <List sx={{
                    }}>
                        {Playlist.map(video => (
                            <ListItem key={video.file}>
                                <ListItemButton onClick={() => videoSelect(video)}
                                    sx={video.file === curVideo?.file ? { border: 1, borderColor: `${blueGrey[100]}` } : {}}>
                                    <ListItemText>{video.title} - {video.genre} {video.length}</ListItemText>
                                </ListItemButton>
                            </ListItem>
                        ))}
                    </List>
                </Box>
            </Section>
            <Footer />
        </Screen>
    );
}

export default VideoWorksPage;
