import { styled } from '@mui/material/styles';
import { ReactNode, useState } from 'react';
import nebula from './nebula_dark.png';
import { Box } from '@mui/material';
import { blueGrey } from '@mui/material/colors';
import { Link as RouterLink } from 'react-router-dom';

export const Link: React.FC<{ children: ReactNode, to: string, props?: any }> = ({ children, to, ...props }) => {
    const [isHover, setIsHover] = useState(false);

    const handleMouseEnter = () => {
        setIsHover(true);
    }

    const handleMouseLeave = () => {
        setIsHover(false);
    }

    return (
        <RouterLink
            style={{
                textDecoration: isHover ? 'underline' : 'none',
                color: isHover ? blueGrey[300] : blueGrey[500],
                fontWeight: 600,
            }}
            to={to}
            onMouseEnter={handleMouseEnter}
            onMouseLeave={handleMouseLeave}
            {...props}
        >
            {children}
        </RouterLink>
    )
}

interface SectionProps {
    disableGutters?: boolean;
}

export const Section = styled('div')<SectionProps>(({ theme, disableGutters }) => ({
    paddingLeft: 20,
    paddingBottom: 28,

    /*
    paddingLeft: (disableGutters === true ? 0 : 16),
    paddingRight: (disableGutters === true ? 0 : 16),
    paddingTop: 16,
    paddingBottom: 16,
*/
}));

export const Screen: React.FC<{ children: ReactNode }> = ({ children }) => {

    return (
        <Box sx={{
            height: '100vh',
            background: `url(${nebula}) no-repeat center fixed`,
            display: 'flex',
            flexDirection: 'column',
        }}>
            <Box sx={{
                py: 8,
                px: 8,
                flex: 1,
                display: 'flex',
                flexDirection: 'column',
            }}>
                <Box sx={{
                    borderLeft: `1px solid ${blueGrey[600]}`,
                    px: 4,
                    py: 4,
                    flex: 1,
                    display: 'flex',
                    flexDirection: 'column',
                }}>
                    {children}
                </Box>
            </Box>
        </Box >
    );
}