import React from 'react';
import { createBrowserRouter, Navigate, Outlet, RouterProvider } from "react-router-dom";
import '@fontsource/roboto/300.css';
import '@fontsource/roboto/400.css';
import '@fontsource/roboto/500.css';
import '@fontsource/roboto/700.css';
import './App.css';

import theme from './lib/theme';
import { ThemeProvider } from "@mui/material/styles";

import Home from "./pages/home";
import Discography from './pages/discography';
import AudioWorks from './pages/audio-works';
import RadioPage from './pages/radio';
import BioPage from './pages/biography';
import ContactPage from './pages/contact';
import VideoWorksPage from './pages/video-works';

function publicRoutes() {
  return [
    {
      path: "/",
      element: <Home />
    }, {
      path: "/discography",
      element: <Discography />
    }, {
      path: "/audio-works",
      element: <AudioWorks />
    }, {
      path: "/video-works",
      element: <VideoWorksPage />
    }, {
      path: "/biography",
      element: <BioPage />
    }, {
      path: "/contact",
      element: <ContactPage />
    }, {
      path: "/radio",
      element: <RadioPage />
    }, {
      path: "*",
      element: <Navigate to="/" replace />
    },
  ];
}

const App: React.FC = () => {
  const routes = publicRoutes();
  const router = createBrowserRouter(routes);

  return (
    <div style={{ minHeight: '100vh', color: theme.palette.text.primary, backgroundColor: theme.palette.background.default }}>
      <ThemeProvider theme={theme}>
        <RouterProvider router={router} />
      </ThemeProvider>
    </div>
  )
}

export default App;
