import React, { useContext, useEffect, useRef, useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';

import { styled } from '@mui/material/styles';
import { Box, Typography, Grid, List, ListItem, Divider, Card, CardActionArea, CardMedia, CardContent, CardActions, Button, Dialog, DialogTitle, DialogContent, TextField, DialogActions } from '@mui/material';
import { useTheme } from '@mui/material/styles';
import Header from '../components/header';
import { Screen, Section } from '../components/screen';
import Footer from '../components/footer';
import { WorkType, works } from '../lib/dicography-base';
import mediumZoom from 'medium-zoom';
import { blueGrey } from '@mui/material/colors';

interface ZoomImageProps {
    src: string;
    largeSrc: string;
    alt?: string;
    className?: string;
}

const ZoomImage: React.FC<ZoomImageProps> = ({ src, largeSrc, alt, className }) => {
    const imgRef = useRef<HTMLImageElement | null>(null);

    useEffect(() => {
        let zoom: any | null = null;

        if (imgRef.current) {
            zoom = mediumZoom(imgRef.current, {
                margin: 80,
                background: 'rgba(0, 0, 0, .7)',
                scrollOffset: 0,
            });
        }
        return () => {
            if (zoom) {
                zoom.detach();
            }
        };
    }, [src]);

    return <img src={src} style={{width: '100%'}} data-zoom-src={largeSrc} alt={alt} className={className} ref={imgRef} />;
};

const Work: React.FC<{ work: WorkType }> = ({ work }) => {
    return (
        <Box sx={{
            width: 192,
            height: 256,
        }}>
            <ZoomImage src={work.thumbUrl} largeSrc={work.imageUrl} alt="Description of Image" />
            <Box sx={{width: '100%', my: 1, color: blueGrey[300], fontWeight: 700, fontSize: 20}}>{work.title}</Box>
            <Box sx={{width: '100%', }}>{work.releaseAt}</Box>
        </Box>
    );
}

const Discography: React.FC = () => {
    return (
        <Screen>
            <Header title={"Discography"} />
            <Section>
                <Box sx={{ display: 'flex', flexWrap: 'wrap', rowGap: 12, columnGap: 4}}>
                    {works.map(work => (
                        <Work work={work} />
                    ))}
                </Box>
            </Section>
            <Footer />
        </Screen>
    );
}

export default Discography;
